<template>
    <div>
        <div class="topLogo">
            <div class="topText">MANUFACTURING</div>
            <div class="servcicesItemSmegma"></div>
            <img src="../img/manufacturing.png" alt="">
        </div>

        <div class="contentWrapper">
            <div class="leafMenu">
                <div v-for="(item, index) in leafMenuList" :class="currentMenu == index ? 'active' : ''"
                    class="leafMenuItem" @click="changeCurrentMenu(item, index)" :key="index">
                    {{ item.title || '' }}
                </div>
            </div>
            <div class="rightContent">
                <div class="contentTitle" v-if="leafMenuList[currentMenu] && leafMenuList[currentMenu].title">{{
                    leafMenuList[currentMenu].title || '' }}
                </div>
                <el-divider class="titleDivider"></el-divider>
                <div class="contentDesc" v-if="centerArr != [] && centerArr.options && centerArr.options.length > 0">
                    <div class="contentDescImgWrapper" v-for="(item, index) in centerArr.options" :key="index"
                        @click="toProductsDetails(item)">
                        <img class="contentDescImg" :src="item.img || '../img/companyImg.png'" @error="showdefaultImg(item)"
                            alt="" v-if="showImg">
                        <div class="contentDescTitle">{{ item.title || '' }}</div>
                    </div>
                </div>
                <el-pagination background layout="prev, pager, next" class="pagiation" @current-change="currentChange"
                    :total="leafMenuList[currentMenu].options.length || 0" :page-size="6"  v-if="centerArr != [] && centerArr.options && centerArr.options.length > 0">
                </el-pagination>
            </div>
        </div>

    </div>
</template>
    
<script>
import salesSquare from '../img/salesSquare.png'
import { product, cate } from "@/api/index"

export default {
    data() {
        return {
            leafMenuList: [],
            centerArr: {},
            currentMenu: 0,
            defaultImg: '../img/companyImg.png',
            showImg: true,
        };
    },
    async mounted() {
        await this.getcateList()

        await this.getproductList()
        for (let i = 0; i < this.leafMenuList.length; i++) {
            if (this.leafMenuList[i].id == this.$route.query.type) {
                this.currentMenu = i
                this.currentChange(1)
            }
        }
    },

    methods: {
        async changeCurrentMenu(item, index) {
            this.$router.push({
                query: {
                    type: item.type
                }
            })
            this.currentMenu = index
            await this.getproductList()

            this.currentChange(1)
        },
        toProductsDetails(item) {
            this.$router.push(`/manufacturing?type=${this.centerArr.type}&id=${item.id}`)
            window.scrollTo(0, 0)

        },
        currentChange(value) {
            this.centerArr = {}
            this.centerArr.type = this.leafMenuList[this.currentMenu].type
            this.centerArr.options = this.leafMenuList[this.currentMenu].options.slice(value - 1, 6)
            // document.body.scrollTop = document.documentElement.scrollTop = 0;
        },
        getproductList() {
            let that = this
            return new Promise(function (resolve, reject) {
                product({ cate: that.leafMenuList[that.currentMenu].id }).then(res => {
                    if (res && res.code == 1) {
                        let { list } = res.data
                        that.leafMenuList[that.currentMenu].options = list
                        resolve()
                    }
                })
            })

        },
        getcateList() {
            return new Promise((resolve, reject) => {
                cate().then(res => {
                    if (res && res.code == 1) {
                        let { data } = res
                        this.leafMenuList = data.map(item => {
                            return {
                                ...item,
                                title: item.name,
                                type: item.id,
                                options: [],
                            }
                        })
                        resolve()
                    }
                })

            })

        },
        showdefaultImg(item) {
            item.img = salesSquare
            this.showImg = false
            this.$nextTick(() => {
                this.showImg = true
            })
        }
    }
};
</script>
    
<style lang="less" scoped>
.pagiation {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
}

.servcicesItemSmegma {
    width: 100%;
    height: 100%;
    z-index: 2;
    background: #17457C;
    opacity: 0.2;
    position: absolute;
    top: 0;
    left: 0;
}

.contentWrapper {
    padding: 0 140px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
}

.leafMenu {
    // position: fixed;
    margin-right: 110px;
}

.contentDesc {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.leafMenuItem {
    width: 316px;
    height: 54px;
    margin-bottom: 20px;
    font-size: 24px;
    // 
    font-weight: 400;
    color: #FFFFFF;
    background-color: rgb(17, 88, 232);
    text-align: center;
    line-height: 54px;
    cursor: pointer;
}

.leafMenuItem:hover {
    background-color: rgba(17, 88, 232, 0.5);

}

.active {
    background-color: rgba(0, 0, 0, 0.3);
}

.active:hover {
    background-color: rgba(0, 0, 0, 0.3);

}

.contentDescTitle {
    font-size: 22px;
    font-weight: 500;
    color: #FF0000;
    text-align: justify;

    word-wrap: break-word;
    word-break: break-all;
    hyphens: auto;
}

.contentDescImgWrapper {
    width: 360px;
    margin-right: 67px;
    margin-bottom: 36px;
    cursor: pointer;

}

.contentDescImgWrapper:nth-of-type(3n) {
    margin-right: 0;
}

.contentDescImg {
    width: 100%;
    height: 245px;
    margin-bottom: 18px;
}

.rightContent {
    flex: 1;
    padding-bottom: 400px;
    position: relative;
}

.titleDivider {
    margin: 11px 0 60px 0;
    height: 3px;
    background-color: #1158e8;

}

.contentTitle {
    font-size: 36px;
    font-weight: 400;
    color: #000000;
    text-align: left;
}

.topText {
    font-size: 60px;
    
    font-weight: 500;
    color: #FFFFFF;
    z-index: 999;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.topLogo {
    width: 1920px;
    height: 331px;
    background: #17457c;
    margin-bottom: 103px;
    position: relative;
}
</style>